<script setup>
import { ref } from "vue"

const showModal = ref(false)

const emit = defineEmits(["confirm"])

uni.$on("showOrderConfirmModel", (event) => {
  showModal.value = true
})

onUnmounted(() => {
  uni.$off("showOrderConfirmModel")
})

const handleClickCheckIt = () => {
  showModal.value = false
  emit('confirm')
}
</script>

<template>
  <wd-popup
    v-model="showModal"
    position="center"
    custom-style="border-radius: 8px"
  >
    <view class="match-wrapper">
      <view class="header">
        <view class="close-icon-button" @click="showModal = false">
          <wd-icon name="close" color="#ffffff" size="16px" />
        </view>
      </view>
      <view class="content">
        By click the button,you confirm that the orde is completed and the action is not revertible
      </view>
      <view class="bottom-line"></view>
      <view class="bottom-box">
        <view class="check-out-button" @click="handleClickCheckIt">
          Confirm>
        </view>
      </view>
    </view>
  </wd-popup>
</template>

