<template>
  <view class="flex items-center power-bar-list">
    <text class="star-icon">★</text>
    <view
      v-for="i in bars"
      :key="i"
      class="power-bar"
      :class="{ 'non-select': i > selectedCount, clickable: clickable }"
      @click="handleSelect(i)"
    ></view>
    <text class="powner-text">{{ displayPercent }}%</text>
  </view>
</template>

<script setup>
/**
 * 评分条组件
 * props:
 *  - percentage: Number 0-100, 百分比
 *  - bars: Number 默认5
 *  - clickable: Boolean 是否可点击
 */

import { computed } from "vue"

const emits = defineEmits(["update:percentage", "change"])

const props = defineProps({
  percentage: {
    type: Number,
    default: 0,
  },
  bars: {
    type: Number,
    default: 5,
  },
  clickable: {
    type: Boolean,
    default: false,
  },
})

// 计算选中的 bar 数
const selectedCount = computed(() => {
  // 确保在 0-100 范围
  const p = Math.max(0, Math.min(props.percentage, 100))
  return Math.round((p / 100) * props.bars)
})

const displayPercent = computed(() => {
  return Math.round(Math.max(0, Math.min(props.percentage, 100)))
})

// 点击某个 bar 时触发
function handleSelect(index) {
  if (!props.clickable) return
  const newPercent = (index / props.bars) * 100
  emits("update:percentage", newPercent)
  emits("change", newPercent)
}
</script>

<style lang="scss" scoped>
.power-bar-list {
  gap: 14rpx;
  width: 100%;
  margin-top: 20rpx;
  margin-bottom: 44rpx;

  .power-bar {
    width: 110rpx;
    height: 20rpx;
    border-radius: 16rpx;
    background: #f6e04e;

    &.clickable {
      cursor: pointer;
    }

    &.non-select {
      background: #b2b2b2;
    }
  }

  .powner-text {
    font-size: 28rpx;
    margin-left: 20rpx;
  }
}
.star-icon {
  color: #235597;
  font-size: 20px;
  margin-right: 5px;
}
</style>
