<template>
  <!-- 页面内容容器（必需） -->
  <view class="orderStep">
    <!-- 安全顶部容器（H5不必） -->
    <wd-toast />
    <!-- 页面内容 -->
    <view class="content">
      <!-- ---------------------- 标题信息 ---------------------- -->
      <view class="order-complete-title">
        <text class="title-text">Order completed!</text>
      </view>
      <!-- ---------------------- 评价等级 ---------------------- -->
      <view class="rating-section">
        <text class="rating-prompt">Rate your experience with Chris!</text>
        <!--<wd-rate v-model="currentRating" />-->
        <PowerRating v-model:percentage="currentRating" clickable/>
      </view>
      <!-- ---------------------- 评价内容输入框 ---------------------- -->
      <view class="review-section">
        <text class="review-prompt">Give your review</text>
        <textarea
          class="review-textarea"
          placeholder="Tell us your experience"
          v-model="reviewContent"
        ></textarea>
      </view>
      <!-- ----------------- 操作按钮部分 ----------------- -->
      <view class="action-buttons">
        <view class="accept-button" @click="handleSubmit">SUBMIT</view>
      </view>
    </view>
  </view>
</template>

<script setup>
import { ref } from "vue"
import { useNavigate } from "@/hooks/useNavigate"
import PowerRating from "@/components/PowerRating.vue"

const emits = defineEmits(['submit'])
// 当前评分
const currentRating = ref(0)
// 评价内容
const reviewContent = ref("")

// 提交评价
const handleSubmit = () => {
  if (!currentRating.value) {
    useToast("Please select the rating first")
    return
  }
  if (!reviewContent.value.trim()) {
    useToast("Please enter the evaluation content")
    return
  }
  // 这里可以添加实际的提交逻辑，比如调用后端接口
  useToast("The evaluation submission was successful.")
  emits("submit")
}
</script>

